import React from "react";
import styled from "styled-components";
import express from "../../../Asset/icon/icons8-express-480.png";

export default function ExpressBox() {
  return (
    <ExpressBoxStyle>
      <div>
        <div class="container">
          <div class="row">
            <div class="">
              <div class="counter green">
                <div class="counter-icon">
                  <img height={40} width={40} src={express} alt="" srcset="" />
                </div>
                <h3>Express</h3>
              </div>
            </div>
          </div>
        </div>
      </div>
    </ExpressBoxStyle>
  );
}

const ExpressBoxStyle = styled.div`




:root{ --main-color:white; }
.counter{
    font-family: 'Secular One', sans-serif;
    text-align: center;
    height: 200px;
   
    padding: 0 25px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
}
.counter:before,
.counter:after{
    content: '';
    background-color: var(--main-color);
    position: absolute;
    left: 0;
    top: 25px;
    right: 50%;
    bottom: 0;
    z-index: -1;
}
.counter:after{
    background-color: #212428;
    border-radius: 0 50px 0 0;
    box-shadow: 5px 5px 25px #00000054;
    left: 15px;
    top: 40px;
    bottom: 15px;
    right: 0;
}
.counter .counter-icon{
    color:  #23bec7;
    background-color: var(--main-color);
    font-size: 30px;
    line-height: 65px;
    width: 60px;
    height: 60px;
    margin: 0 auto 35px;
    border-radius: 50%;
}
.counter h3{
    color:  white;
    font-size: 18px;
    font-weight: 500;
    text-transform: capitalize;
    margin: 0 0 7px;
}
.counter .counter-value{
    color: var(--main-color);
    font-size: 30px;
    font-weight: 600;
    letter-spacing: 1.5px;
    display: block;
}
.counter.green{ --main-color: white; }
.counter.orange{ --main-color: #FF7000; }
.counter.magenta{ --main-color: #CB1C8D; }
@media screen and (max-width:990px){
    .counter{ margin-bottom: 40px; }
`;
