import React from "react";
import styled from "styled-components";

export default function ProfileImage() {
  return (
    <ProfileImageStyle>
      <div class="card profileImgAni ">
        <img
          class="img-fluid"
          src="https://i.ibb.co/QCgzxQK/small-perfile-image.jpg"
          alt=""
        />
        <span class="top"></span>
        <span class="right"></span>
        <span class="bottom"></span>
        <span class="left"></span>
      </div>
    </ProfileImageStyle>
  );
}

const ProfileImageStyle = styled.div`
  *,
  ::before,
  ::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #111111;
  }

  .card {
    position: relative;
    width: 100%;
    height: 100%;
    color: #fff;
    background: transparent;
    overflow: hidden;
    border: none;

    /* border-top: 1px solid rgba(255, 49, 49, 0.5);
    border-right: 1px solid rgba(255, 49, 49, 0.5);
    border-bottom: 1px solid rgba(255, 49, 49, 0.5);
    border-left: 1px solid rgba(255, 49, 49, 0.5); */
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }

  p {
    font-size: 0.95rem;
    text-align: center;
  }

  span {
    position: absolute;
    border-radius: 100vmax;
  }

  .top {
    top: 0;
    left: 0;
    width: 0;
    height: 2px;
    background: linear-gradient(
      90deg,
      transparent 50%,
      rgb(255, 49, 49),
      rgba(255, 49, 49, 0.5)
    );
  }

  .bottom {
    right: 0;
    bottom: 0;
    height: 2px;
    background: linear-gradient(
      90deg,
      rgb(255, 49, 49),
      rgba(255, 49, 49, 0.5),
      transparent 50%
    );
  }

  .right {
    top: 0;
    right: 0;
    width: 2px;
    height: 0;
    background: linear-gradient(
      180deg,
      transparent 30%,
      rgb(255, 49, 49),
      rgba(255, 49, 49, 0.5)
    );
  }

  .left {
    left: 0;
    bottom: 0;
    width: 2px;
    height: 0;
    background: linear-gradient(
      180deg,
      rgb(255, 49, 49),
      rgba(255, 49, 49, 0.5),
      transparent 70%
    );
  }

  .top {
    animation: animateTop 3s ease-in-out infinite;
  }

  .bottom {
    animation: animateBottom 3s ease-in-out infinite;
  }

  .right {
    animation: animateRight 3s linear infinite;
  }

  .left {
    animation: animateLeft 3s ease-in-out infinite;
  }

  @keyframes animateTop {
    25% {
      width: 100%;
      opacity: 1;
    }

    30%,
    100% {
      opacity: 0;
    }
  }

  @keyframes animateBottom {
    0%,
    50% {
      opacity: 0;
      width: 0;
    }

    75% {
      opacity: 1;
      width: 100%;
    }

    76%,
    100% {
      opacity: 0;
    }
  }

  @keyframes animateRight {
    0%,
    25% {
      opacity: 0;
      height: 0;
    }

    50% {
      opacity: 1;
      height: 100%;
    }

    55%,
    100% {
      height: 100%;
      opacity: 0;
    }
  }

  @keyframes animateLeft {
    0%,
    75% {
      opacity: 0;
      bottom: 0;
      height: 0;
    }

    100% {
      opacity: 1;
      height: 100%;
    }
  }

  .profileImgAni {
    animation: profileImg_Ani 6s ease 1s infinite normal forwards;
  }
  @keyframes profileImg_Ani {
    0% {
      transform: translate(0);
    }

    20% {
      transform: translate(2px, -2px);
    }

    40% {
      transform: translate(2px, 2px);
    }

    60% {
      transform: translate(-2px, 2px);
    }

    80% {
      transform: translate(-2px, -2px);
    }

    100% {
      transform: translate(0);
    }
  }
`;
