import styled from "@emotion/styled";
import React from "react";

export default function AboutMeImage() {
  return (
    <AboutMeImageStyle>
      <div class="block">
        <img
          class="img-fluid image-div"
          src="https://i.ibb.co/QCgzxQK/small-perfile-image.jpg"
          alt=""
        />
      </div>
    </AboutMeImageStyle>
  );
}

const AboutMeImageStyle = styled.div`
  .image-div {
    border-radius: 20px;
  }

  .block {
    position: relative;

    background: linear-gradient(0deg, #000, #272727);
  }

  .block:before,
  .block:after {
    content: "";
    position: absolute;
    left: -2px;
    top: -2px;
    background: linear-gradient(
      45deg,
      #929292,
      #f7f7f7,
      #929292,
      #1e1e1e,
      #110e0c,
      #ffffff,
      #d4cccb,
      #dddedd,
      #8e8e8e,
      #a8a8a8
    );
    background-size: 400%;
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    z-index: -1;
    animation: steam 20s linear infinite;
  }

  @keyframes steam {
    0% {
      background-position: 0 0;
    }
    50% {
      background-position: 400% 0;
    }
    100% {
      background-position: 0 0;
    }
  }

  .block:after {
    filter: blur(50px);
  }
`;
