const MyJourneyStyle = styled.div`
  background-color: #212428;

  color: #c4cfde;

  .boxEn {
    animation: boxEnAni 1s ease 0s 1 normal forwards;
  }
  @keyframes boxEnAni {
    0% {
      transform: scaleX(2);
    }

    100% {
      transform: scaleX(1);
    }
  }

  .aboutMe_me_div {
    animation: aboutMe_me_div_ani 2s ease 0s infinite normal forwards;
  }
  @keyframes aboutMe_me_div_ani {
    0%,
    100% {
      transform: translateX(0%);
      transform-origin: 50% 50%;
    }

    15% {
      transform: translateX(-30px) rotate(-6deg);
    }

    30% {
      transform: translateX(15px) rotate(6deg);
    }

    45% {
      transform: translateX(-15px) rotate(-3.6deg);
    }

    60% {
      transform: translateX(9px) rotate(2.4deg);
    }

    75% {
      transform: translateX(-6px) rotate(-1.2deg);
    }
  }

  .timeline {
    background: white;
    counter-reset: section;
    margin: 20px auto;
    padding-left: 70px;
    padding-top: 50px;
  }

  .timeline .col-xs-12 {
    border-left: solid 4px orange;
    padding-bottom: 80px;
    padding-left: 40px;
    padding-right: 60px;
  }

  .timeline .col-xs-12:before {
    /* counter */
    counter-increment: section;
    content: counter(section);
    /* position */
    position: absolute;
    left: -22px; /* = (width + border-left) / 2 */
    top: -30px;
    /* form & size */
    border-radius: 50%;
    height: 40px;
    width: 40px;
    /* colors & text */
    background: orange;
    color: white;
    font-size: 22px;
    font-weight: bold;
    line-height: 40px;
    text-align: center;
  }

  /* images */
  .timeline img {
    margin-top: 20px;
    width: 100%;
  }
  @media (max-width: 480px) {
    .col-xs-6 {
      width: 100% !important;
    }
  }
`;
