import React from "react";
import styled from "styled-components";

export default function Hobby() {
  return (
    <HobbyStyle>
      <div className="p-3 container">
        <div>
          <h1 className="aboutMeTitle d-flex justify-content-center">
            <div>My</div>
            <div className="aboutMe_me_div px-2" style={{ color: "red" }}>
              Hobby
            </div>{" "}
          </h1>
        </div>

        <div className="mt-5">
          <div className="row">
            <div className="col-md-4 col-6">
              <div className="box-div">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/blog/blog-01.jpg"
                    class="img-fluid box-image"
                    alt=""
                  />
                </div>
                <div className="box-title">Football</div>
              </div>
            </div>
            <div className="col-md-4 col-6">
              <div className="box-div">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/blog/blog-01.jpg"
                    class="img-fluid box-image"
                    alt=""
                  />
                </div>
                <div className="box-title">Digital Art</div>
              </div>
            </div>
            <div className="col-md-4 col-6">
              <div className="box-div">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/blog/blog-01.jpg"
                    class="img-fluid box-image"
                    alt=""
                  />
                </div>
                <div className="box-title">Programming</div>
              </div>
            </div>
            <div className="col-md-4 col-6">
              <div className="box-div">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/blog/blog-01.jpg"
                    class="img-fluid box-image"
                    alt=""
                  />
                </div>
                <div className="box-title">Photography</div>
              </div>
            </div>
            <div className="col-md-4 col-6">
              <div className="box-div">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/blog/blog-01.jpg"
                    class="img-fluid box-image"
                    alt=""
                  />
                </div>
                <div className="box-title">Whiting</div>
              </div>
            </div>
            <div className="col-md-4 col-6">
              <div className="box-div">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/blog/blog-01.jpg"
                    class="img-fluid box-image"
                    alt=""
                  />
                </div>
                <div className="box-title">Learning</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </HobbyStyle>
  );
}

const HobbyStyle = styled.div`
  background-color: #212428;

  color: #c4cfde;

  .aboutMe_me_div {
    animation: aboutMe_me_div_ani 2s ease 0s infinite normal forwards;
  }
  @keyframes aboutMe_me_div_ani {
    0%,
    100% {
      transform: translateX(0%);
      transform-origin: 50% 50%;
    }

    15% {
      transform: translateX(-30px) rotate(-6deg);
    }

    30% {
      transform: translateX(15px) rotate(6deg);
    }

    45% {
      transform: translateX(-15px) rotate(-3.6deg);
    }

    60% {
      transform: translateX(9px) rotate(2.4deg);
    }

    75% {
      transform: translateX(-6px) rotate(-1.2deg);
    }
  }

  .box-div {
    margin: 10px 0px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
  }
  .box-title {
    margin-top: 10px;
  }

  .box-image {
    border-radius: 10px;
  }
`;
