import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import bg_copy_4 from "../../Asset/BgImage/butterfly/Background_copy_4.png";
import bg_copy_5 from "../../Asset/BgImage/butterfly/Background_copy_5.png";
import butterfly from "../../Asset/BgImage/butterfly/Butterfly.png";
import layout_20 from "../../Asset/BgImage/butterfly/Layer_20.png";
import layout_23 from "../../Asset/BgImage/butterfly/Layer_23.png";
import layout_effect from "../../Asset/BgImage/butterfly/lighting.png";

import ArrowIcon from "../../Asset/icon/ArrowIcon.png";

export default function ClickBtn() {
  let history = useHistory();

  const [isLoading, setIsLoading] = useState(false);

  return (
    <ClickBtnBack>
      <div style={{ position: "relative" }}>
        <div className="ArrowIconText">
          <span>
            {isLoading === true ? "Loading..." : "Touch the Butterfly"}
          </span>
        </div>
        <div className="ArrowIcon">
          <div>
            <div>
              <img className="" src={ArrowIcon} alt="" />
            </div>
          </div>
        </div>
        {/* <img className="layout_effect" src={layout_effect} alt="" srcset="" /> */}
        <img className="layout_effect" src={layout_effect} alt="" srcset="" />
        <img className="Layer_23" src={layout_23} alt="" srcset="" />
        <img
          className="Layer_23"
          style={{ opacity: "0.5" }}
          src={layout_23}
          alt=""
          srcset=""
        />
        <img
          className="Layer_23"
          style={{ opacity: "0.5" }}
          src={layout_23}
          alt=""
          srcset=""
        />
        <img className="Background_copy_4" src={bg_copy_4} alt="" srcset="" />
        <img className="Background_copy_5" src={bg_copy_5} alt="" srcset="" />
        <img className="butter" src={butterfly} alt="" srcset="" />
        <img
          onClick={() => {
            history.push("/Home");
            setIsLoading(true);
          }}
          className="Layer_20"
          src={layout_20}
          alt=""
          srcset=""
        />
      </div>
    </ClickBtnBack>
  );
}

const ClickBtnBack = styled.div`
  .butter {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 200px;
    display: none;
    z-index: 53;
  }
  .Background_copy_5 {
    background-image: url("Background copy 5.png");
    opacity: 0.588;
    position: absolute;
    left: 2px;
    top: 0px;
    width: 200px;
    display: none;

    z-index: 52;
  }
  .Background_copy_4 {
    background-image: url("Background copy 4.png");
    opacity: 0.761;
    position: absolute;
    left: 392px;
    top: 391px;
    width: 200px;
    display: none;

    z-index: 51;
  }

  .Layer_20 {
    background-image: url("Layer 20.png");
    position: absolute;
    left: 454px;
    top: 411px;
    cursor: pointer;
    animation: butterFlyAni 3s ease 0s infinite normal none;
    z-index: 48;
  }
  .Layer_23 {
    position: absolute;
    top: 292px;
    left: 333px;
    animation: butterFlyAni 3s ease 0s infinite normal none;
  }
  .layout_effect {
    position: absolute;
    top: 341px;
    left: 401px;
    filter: invert(1);
    color: yellow;
    width: 169px;
    animation: butterFlyAni 4s ease 0s infinite normal none;
  }
  .ArrowIcon {
    top: 239px;
    left: 471px;
    position: absolute;
    animation: butterFlyAni 5s ease 0s infinite normal none;
  }
  .ArrowIconText {
    top: 220px;
    left: 541px;
    width: 200px;
    position: absolute;
    animation: butterFlyAni 3s ease 0s infinite normal none;
  }
  @keyframes butterFlyAni {
    0% {
      transform: translate(0);
    }

    20% {
      transform: translate(-2px, 2px);
    }

    40% {
      transform: translate(-2px, -2px);
    }

    60% {
      transform: translate(2px, 2px);
    }

    80% {
      transform: translate(2px, -2px);
    }

    100% {
      transform: translate(0);
    }
  }

  @media screen and (max-width: 767px) {
    .Layer_20 {
      background-image: url("Layer 20.png");
      position: absolute;
      top: 482px;
      left: 213px;
      cursor: pointer;
      animation: butterFlyAni 3s ease 0s infinite normal none;
      z-index: 48;
    }
    .Layer_23 {
      position: absolute;
      top: 362px;
      left: 113px;
      animation: butterFlyAni 3s ease 0s infinite normal none;
    }
    .layout_effect {
      position: absolute;
      top: 402px;
      left: 153px;
      filter: invert(1);
      color: yellow;
      width: 169px;
    }

    .ArrowIcon {
      top: 302px;
      left: 229px;
    }
    .ArrowIconText {
      top: 272px;
      left: 153px;
      width: 200px;
    }
  }
`;
