import React from "react";
import styled from "styled-components";
import Branding from "./Box/Branding";
import DesktopApp from "./Box/DesktopApp";
import FrontDesign from "./Box/FrontDesign";
import MobileApp from "./Box/MobileApp";
import PhotoEdit from "./Box/PhotoEdit";
import Ui_Ux from "./Box/Ui_Ux";
import WebApp from "./Box/WebApp";

export default function MyPortfolio() {
  return (
    <MyPortfolioStyle>
      <div className=" container mt-2 pt-3" style={{ color: "white" }}>
        <div>
          <h1 className="MyPortfolio d-flex justify-content-center">
            <div>My</div>
            <div className="MyPortfolio_div px-2" style={{ color: "#ff014f" }}>
              Portfolio
            </div>{" "}
          </h1>
        </div>
        <div className="designForDesktop">
          <div className="row">
            <div className="col-6 col-md-4 mt-4 d-flex justify-content-center">
              <div className="Main-box">
                <Branding></Branding>
              </div>
            </div>
            <div className="col-6 col-md-4 mt-4 d-flex justify-content-center">
              <div className="Main-box">
                <PhotoEdit></PhotoEdit>
              </div>
            </div>
            <div className="col-6 col-md-4 mt-4 d-flex justify-content-center">
              <div className="Main-box">
                <FrontDesign></FrontDesign>
              </div>
            </div>
            <div className="col-6 col-md-3 mt-4 d-flex justify-content-center">
              <div className="Main-box">
                <Ui_Ux></Ui_Ux>
              </div>
            </div>
            <div className="col-6 col-md-3 mt-4 d-flex justify-content-center">
              <div className="Main-box">
                <WebApp></WebApp>
              </div>
            </div>
            <div className="col-6 col-md-3 mt-4 d-flex justify-content-center">
              <div className="Main-box">
                <MobileApp></MobileApp>
              </div>
            </div>
            <div className="col-6 col-md-3 mt-4 d-flex justify-content-center">
              <div className="Main-box">
                <DesktopApp></DesktopApp>
              </div>
            </div>
          </div>
        </div>
        <div className="designForMobile">
          <div className="row">
            <div className="col-6 col-md-4 mt-4 ">
              <div className="Main-box">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/portfolio/portfolio-01.jpg"
                    class="img-fluid proImage"
                    alt=""
                  />
                </div>
                <div className="info-box">
                  <div className="main-info">
                    <span>Branding</span>
                  </div>
                  <div className="sub-info">
                    <span>GRAPHIC DESIGN</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-6 col-md-4 mt-4 ">
              <div className="Main-box">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/portfolio/portfolio-01.jpg"
                    class="img-fluid proImage"
                    alt=""
                  />
                </div>
                <div className="info-box">
                  <div className="main-info">
                    <span>Photo Edit</span>
                  </div>
                  <div className="sub-info">
                    <span>DIGITAL ART</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-6 col-md-4 mt-4 ">
              <div className="Main-box">
                <div>
                  <img
                    class="img-fluid proImage"
                    src="https://rainbowit.net/html/inbio/assets/images/portfolio/portfolio-01.jpg"
                    alt=""
                  />
                </div>
                <div className="info-box">
                  <div className="main-info">
                    <span>Front design</span>
                  </div>
                  <div className="sub-info">
                    <span>TYPO DESIGN</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-6 col-md-4 mt-4 ">
              <div className="Main-box">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/portfolio/portfolio-01.jpg"
                    class="img-fluid proImage"
                    alt=""
                  />
                </div>
                <div className="info-box">
                  <div className="main-info">
                    <span>UI/UX</span>
                  </div>
                  <div className="sub-info">
                    <span>PRODUCT DESIGN</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-6 col-md-4 mt-4">
              <div className="Main-box">
                <div>
                  <img
                    src="https://rainbowit.net/html/inbio/assets/images/portfolio/portfolio-01.jpg"
                    class="img-fluid proImage"
                    alt=""
                  />
                </div>
                <div className="info-box">
                  <div className="main-info">
                    <span>Web App</span>
                  </div>
                  <div className="sub-info">
                    <span>WEB DEVELOPMENT</span>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-6 col-md-4 mt-4 ">
              <div className="Main-box">
                <div>
                  <img
                    class="img-fluid proImage"
                    src="https://rainbowit.net/html/inbio/assets/images/portfolio/portfolio-01.jpg"
                    alt=""
                  />
                </div>
                <div className="info-box">
                  <div className="main-info">
                    <span>Mobile App</span>
                  </div>
                  <div className="sub-info">
                    <span>APP DEVELOPMENT</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </MyPortfolioStyle>
  );
}

const MyPortfolioStyle = styled.div`
  .MyPortfolio {
    color: #c4cfde;
  }

  .MyPortfolio_div {
    animation: MyPortfolio_div_ani 2s ease 0s infinite normal forwards;
  }
  @keyframes MyPortfolio_div_ani {
    0%,
    100% {
      transform: translateX(0%);
      transform-origin: 50% 50%;
    }

    15% {
      transform: translateX(-30px) rotate(-6deg);
    }

    30% {
      transform: translateX(15px) rotate(6deg);
    }

    45% {
      transform: translateX(-15px) rotate(-3.6deg);
    }

    60% {
      transform: translateX(9px) rotate(2.4deg);
    }

    75% {
      transform: translateX(-6px) rotate(-1.2deg);
    }
  }

  .Main-box {
    padding: 15px;
    border-radius: 10px;
    background: linear-gradient(145deg, #23272b, #1e2024);
    box-shadow: 9px 9px 11px #191c1f, -9px -9px 11px #292c31;

    align-items: center;
  }
  .proImage {
    border-radius: 5px;
  }
  .info-box {
    color: #c4cfde;
    font-weight: 600;
    font-size: 23px;
    line-height: 34px;
    text-align: left;
    margin-top: 10px;
  }
  .designForMobile {
    display: none;
  }

  @media screen and (max-width: 680px) {
    .designForMobile {
      display: block;
    }
    .designForDesktop {
      display: none;
    }
    .main-info {
      color: #ff014f;
      font-weight: 200px;
      font-size: 12px;
      letter-spacing: 1px;
    }
    .sub-info {
      color: #c4cfde;
      font-weight: 600;
      font-size: 15px;
      margin-top: -10px;
    }
  }
`;
