import React, { useEffect } from "react";
import styled from "styled-components";
import Hobby from "../Hobby/Hobby";
import HomeAboutMe from "../HomeComponents/HomeAboutMe/HomeAboutMe";
import MySkillMobile1 from "../MySkill/MySkillMobile1";
import MySkillMobile2 from "../MySkill/MySkillMobile2";
import MySkillMobile3 from "../MySkill/MySkillMobile3";

import { useInView } from "react-intersection-observer";
import { useHistory } from "react-router-dom";
import MyJourney from "../MyJourney/MyJourney";

export default function FullPage() {
  let history = useHistory();

  const handleClickScroll = () => {
    const element = document.getElementById("section-1");
    if (element) {
      // 👇 Will scroll smoothly to the top of the next section
      element.scrollIntoView({ behavior: "smooth" });
    }
  };

  useEffect(() => {
    handleClickScroll();
  }, []);

  const { ref: refHobby, inView: isViewHobby } = useInView({
    /* Optional options */
    threshold: 0,
  });
  const { ref: refHomeAboutMeEn, inView: IsViewHomeAboutMe } = useInView({
    /* Optional options */
    threshold: 0,
  });
  const { ref: refOne, inView: inViewOne } = useInView({
    /* Optional options */
    threshold: 0,
  });
  const { ref: refTwo, inView: inViewTwo } = useInView({
    /* Optional options */
    threshold: 0,
  });
  const { ref: refThree, inView: inViewThree } = useInView({
    /* Optional options */
    threshold: 0,
  });

  return (
    <FullPageStyle>
      <div id="hero-section">
        <button
          style={{ height: "100vh", width: "100%", backgroundColor: "red" }}
          className="btn-scroll"
          onClick={() => {
            history.push("/");
          }}
        >
          Back To Old Page
        </button>
      </div>
      <div
        id="section-1"
        className={isViewHobby && "hobbyAni"}
        ref={refHobby}
        style={{ opacity: 0 }}
      >
        {" "}
        <Hobby></Hobby>
      </div>

      <MyJourney></MyJourney>

      <div
        className={IsViewHomeAboutMe && "HomeAboutMeEn"}
        ref={refHomeAboutMeEn}
        style={{ opacity: 0 }}
      >
        <HomeAboutMe></HomeAboutMe>
      </div>
      <div className={inViewOne && "boxEx"} ref={refOne} style={{ opacity: 0 }}>
        <MySkillMobile1></MySkillMobile1>
      </div>
      <div className={inViewTwo && "boxEx"} ref={refTwo} style={{ opacity: 0 }}>
        <MySkillMobile2></MySkillMobile2>
      </div>
      <div
        className={inViewThree && "boxEx"}
        ref={refThree}
        style={{ opacity: 0 }}
      >
        <MySkillMobile3></MySkillMobile3>
      </div>
    </FullPageStyle>
  );
}

const FullPageStyle = styled.div`
  height: 100vh;
  overflow-y: scroll;
  background-color: #212428;

  color: #c4cfde;

  .hobbyAni {
    animation: hobbyAniKey 1s ease 0s 1 normal forwards;
    opacity: 1;
  }

  @keyframes hobbyAniKey {
    0% {
      opacity: 0;
      transform: translateX(250px);
    }

    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }

  .HomeAboutMeEn {
    animation: myAnim 1s ease 0s 1 normal forwards;
    opacity: 1;
  }

  .boxEx {
    animation: myAnim 1s ease 0s 1 normal forwards;
    opacity: 1;
  }

  @keyframes myAnim {
    0% {
      opacity: 0;
      transform: translateX(-250px);
    }

    100% {
      opacity: 1;
      transform: translateX(0);
    }
  }
`;
