import React from "react";
import styled from "styled-components";

export default function WebApp() {
  return (
    <WebAppStyle>
      <div class="box">
        <img src="https://rainbowit.net/html/inbio/assets/images/portfolio/portfolio-01.jpg" />
        <div class="box-content">
          <h3 class="title">Web App</h3>
          <span class="post">Website App development</span>
        </div>
        <ul class="icon">
          <li>
            <a href="#">
              <i class="fa fa-search"></i>
            </a>
          </li>
          <li>
            <a href="#">
              <i class="fa fa-link"></i>
            </a>
          </li>
        </ul>
      </div>
    </WebAppStyle>
  );
}

const WebAppStyle = styled.div`

       
.box{
    background:#0e204d;
    font-family: 'Josefin Sans', sans-serif;
    text-align: center;
    border-radius: 15px;
    overflow: hidden;
    position: relative;
    transition: all 0.3s ease 0s;
}
/* .box:hover{ box-shadow: 0 0 5px rgba(0, 0, 0, 0.5); } */
.box img{
    width: 100%;
    height: auto;
    transition: all .6s ease-in-out;
}
.box:hover img{
    opacity: 0.4;
    transform: scale(1.3);
}
.box .box-content{
    color: #fff;
    background: #212428;
    width: 100%;
    padding: 15px 15px 10px;
    transform: translateX(-50%);
    position: absolute;
    bottom: -70px;
    left: 50%;
    transition: all 0.4s ease-out;
}
.box:hover .box-content{ bottom: 0; }
.box .title{
    font-size: 22px;
    font-weight: 600;
    text-transform: uppercase;
    margin: 0 0 3px;
}
.box .post{
    font-size: 12px;
    font-weight: 400;
    letter-spacing: 1px;
    text-transform: uppercase;
    display: block;
}
.box .icon{
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    opacity: 0;
    transform: translateX(-50%);
    position: absolute;
    top: 35%;
    left: 50%;
    transition: all 0.4s ease-out;
}
.box:hover .icon{ opacity: 1; }
.box .icon li{
    margin: 0 3px;
    display: inline-block;
    transition: all .5s ease-in-out;
}
.box .icon li:first-child{ transform: translateX( -100%) translateY(-100%); }
.box .icon li:last-child{ transform: translateX( 100%) translateY(100%); }
.box:hover .icon li{ transform: translateX(0) translateY(0); }
.box .icon li a{
    color: #fff;
    background: #13c5dd;
    font-size: 18px;
    line-height: 39px;
    height: 35px;
    width: 35px;
    border-radius: 5px;
    display: block;
    transition: all .5s ease-in-out;
}
.box .icon li a:hover{
    color: #0e204d;
    background: #fff;
}
@media only screen and (max-width:990px){
    .box{ margin: 0 0 30px; }
`;
