import styled from "@emotion/styled";
import React from "react";

import { TypeAnimation } from "react-type-animation";
import HomeAboutMeImage from "./HomeAboutMeImage/HomeAboutMeImage";

export default function HomeAboutMe() {
  return (
    <HomeAboutMeStyle>
      <div className=" mobileDev container pt-2 pt-3">
        <div>
          <h1 className="aboutMeTitle d-flex justify-content-center">
            <div>About</div>
            <div className="aboutMe_me_div px-2" style={{ color: "red" }}>
              Me
            </div>{" "}
          </h1>
        </div>

        <div className=" mt-4">
          <div className="ProfileImage-div">
            <div className=" ProfileImage">
              {" "}
              <HomeAboutMeImage></HomeAboutMeImage>
            </div>
          </div>
        </div>
        <div className="mt-5 p-1">
          <span
            style={{
              fontSize: "16px",
              fontWeight: "400",
              color: "white",
            }}
          >
            <TypeAnimation
              sequence={[
                `Lorem ipsum dolor sit amet, consectetur adipisicing elit. Omnis laboriosam, facilis quo exercitationem dignissimos officiis. Repellat, sit. Ab, debitis nesciunt ducimus pariatur consectetur perspiciatis iste laudantium amet velit, quos quod quisquam deserunt magni error molestiae at magnam? Autem sed ducimus doloribus quas dolor illum repellat minima doloremque ex adipisci voluptate nam, voluptatibus deleniti distinctio architecto reprehenderit modi vitae asperiores, alias eos, accusamus voluptates ipsum ab. Dolorem fuga at quam, nobis asperiores,`,
                1000,
                "",
              ]}
              speed={40}
              wrapper="span"
              repeat={Infinity}
            />
          </span>
        </div>
      </div>
      <div className=" DesktopDev container pt-2 pt-3">
        <div>
          <h1 className="aboutMeTitle d-flex justify-content-center">
            <div>About</div>
            <div className="aboutMe_me_div px-2" style={{ color: "red" }}>
              Me
            </div>{" "}
          </h1>
        </div>
        <div className="px-4 row mt-5 w-100">
          <div className="col-8">
            <div className=" p-1 " style={{ textAlign: "left" }}>
              <span
                style={{
                  fontSize: "16px",
                  fontWeight: "400",
                  color: "#c4cfde",
                }}
              >
                <TypeAnimation
                  sequence={[
                    `Lorem ipsum dolor sit amet, consectetur adipisicing elit. Omnis laboriosam, facilis quo exercitationem dignissimos officiis. Repellat, sit. Ab, debitis nesciunt ducimus pariatur consectetur perspiciatis iste laudantium amet velit, quos quod quisquam deserunt magni error molestiae at magnam? Autem sed ducimus doloribus quas dolor illum repellat minima doloremque ex`,
                    1000,
                    "",
                  ]}
                  speed={40}
                  wrapper="span"
                  repeat={Infinity}
                />
              </span>
            </div>
          </div>
          <div className="col-4">
            <div className="ProfileImage-div">
              <div className=" ProfileImage">
                {" "}
                <HomeAboutMeImage></HomeAboutMeImage>
              </div>
            </div>
          </div>
        </div>

        <div className=" mt-4"></div>
      </div>
    </HomeAboutMeStyle>
  );
}

const HomeAboutMeStyle = styled.div`
  color: #c4cfde;
  .aboutMeTitle {
    color: #d3cbcb;
  }
  @media screen and (max-width: 680px) {
    .DesktopDev {
      display: none;
    }
    .ProfileImage {
      width: 40%;
    }

    .ProfileImage-div {
      display: flex;
      justify-content: space-around;
    }
  }
  @media screen and (min-width: 680px) {
    .mobileDev {
      display: none;
    }
    .DesktopDev {
      display: block;
    }
    .ProfileImage {
      border: 20px solid #212428;
      border-radius: 20px;
    }
  }

  .aboutMe_me_div {
    animation: aboutMe_me_div_ani 2s ease 0s infinite normal forwards;
  }
  @keyframes aboutMe_me_div_ani {
    0%,
    100% {
      transform: translateX(0%);
      transform-origin: 50% 50%;
    }

    15% {
      transform: translateX(-30px) rotate(-6deg);
    }

    30% {
      transform: translateX(15px) rotate(6deg);
    }

    45% {
      transform: translateX(-15px) rotate(-3.6deg);
    }

    60% {
      transform: translateX(9px) rotate(2.4deg);
    }

    75% {
      transform: translateX(-6px) rotate(-1.2deg);
    }
  }
`;
