import React from "react";
import styled from "styled-components";

export default function HomeAboutMeImage() {
  return (
    <HomeAboutMeImageStyle>
      <div class="block">
        <img
          class="img-fluid image-div"
          src="https://i.ibb.co/QCgzxQK/small-perfile-image.jpg"
          alt=""
        />
      </div>
    </HomeAboutMeImageStyle>
  );
}
const HomeAboutMeImageStyle = styled.div`
  .image-div {
    border-radius: 20px;
    padding: 10px;
    box-shadow: 10px 10px 19px #1c1e22, -10px -10px 19px #262a2e;
  }
`;
