import $ from "jquery";
import React, { Component } from "react";
import AboutMe from "./components/AboutMe/AboutMe";
import ClickBtn from "./components/ClickBtn/ClickBtn";
import ClickMeMobile from "./components/ClickMeMobile/ClickMeMobile";
import ContactWithMe from "./components/ContactWithMe/ContactWithMe";
import IntroduceSec from "./components/IntroduceDiv/IntroduceSec";
import MyPortfolio from "./components/MyPortfolio/MyPortfolio";
import MySkillDesktop from "./components/MySkill/MySkillDesktop";
import "./index.css";
import "./jquery.pagepiling";
import "./jquery.pagepiling.css";

export default class Main extends Component {
  componentDidMount() {
    $(document).ready(function () {
      $("#pagepiling").pagepiling({
        navigation: {
          textColor: "#fff",
          bulletsColor: "#fff",
          position: "right",
          // tooltips: ["Home", "About Me", "My Skills", "Soil", "Sky", "Sky"],
        },
      });
    });
  }

  render() {
    return (
      <div>
        <div id="pagepiling">
          <div class="section sec1">
            <div>
              <IntroduceSec></IntroduceSec>
            </div>
          </div>
          <div class="section sec2">
            <AboutMe></AboutMe>
          </div>
          {window.innerWidth > 767 && (
            <div class="section sec3 ">
              {window.innerWidth}
              <MySkillDesktop></MySkillDesktop>
            </div>
          )}

          <div class="section sec5">
            <MyPortfolio></MyPortfolio>
          </div>

          {window.innerWidth > 767 && (
            <div class="section sec4">
              <ContactWithMe></ContactWithMe>
            </div>
          )}

          {window.innerWidth > 767 ? (
            <div class="section sec6">
              <div style={{ color: "white" }}>
                <ClickBtn></ClickBtn>
              </div>
            </div>
          ) : (
            <div
              class="section sec7  "
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <div style={{ color: "white" }}>
                <ClickMeMobile></ClickMeMobile>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}
