import React from "react";
import styled from "styled-components";
import CBox from "./MySkillList/CBox";
import CssBox from "./MySkillList/CssBox";
import ExpressBox from "./MySkillList/ExpressBox";
import FigmaBox from "./MySkillList/FigmaBox";
import FirebaseBox from "./MySkillList/FirebaseBox";
import GithubBox from "./MySkillList/GitbuBox";
import HtmlBox from "./MySkillList/HtmlBox";
import IllustratorBox from "./MySkillList/IllustratorBox";
import JavaBox from "./MySkillList/JavaBox";
import JavaScriptBox from "./MySkillList/JavascriptBox";
import MongoDbBox from "./MySkillList/MongoDb";
import MySqlBox from "./MySkillList/MySqlBox";
import NodeJsBox from "./MySkillList/NodeJsBox";
import PhotoshopBox from "./MySkillList/PhotoshopBox";
import PythonBox from "./MySkillList/PythonBox";
import ReactBox from "./MySkillList/ReactBox";
import ReactNativeBox from "./MySkillList/ReactNativeBox";
import XdBox from "./MySkillList/XdBox";

export default function MySkill() {
  return (
    <MySkillStyle>
      <div className=" container mt-1 pt-3" style={{ color: "white" }}>
        <div>
          <h1 className="MySkillTitle d-flex justify-content-center">
            <div>My</div>
            <div className="MySkillTitle_div px-2" style={{ color: "#ff014f" }}>
              Skills
            </div>{" "}
          </h1>
        </div>
        <div className="row">
          <div className="col-6 col-md-2  ">
            <PhotoshopBox></PhotoshopBox>
          </div>
          <div className="col-6 col-md-2  ">
            <IllustratorBox></IllustratorBox>
          </div>
          <div className="col-6 col-md-2 ">
            <XdBox></XdBox>
          </div>
          {/* <div className="col-6 col-md-2 ">
            <CanvaBox></CanvaBox>
          </div> */}
          <div className="col-6 col-md-2 ">
            <FigmaBox></FigmaBox>
          </div>
          <div className="col-6 col-md-2 ">
            <JavaScriptBox></JavaScriptBox>
          </div>
          <div className="col-6 col-md-2 ">
            <JavaBox></JavaBox>
          </div>
          <div className="col-6 col-md-2 ">
            <CBox></CBox>
          </div>
          <div className="col-6 col-md-2 ">
            <PythonBox></PythonBox>
          </div>
          <div className="col-6 col-md-2 ">
            <HtmlBox></HtmlBox>
          </div>
          <div className="col-6 col-md-2 ">
            <CssBox></CssBox>
          </div>
          <div className="col-6 col-md-2 ">
            <ReactBox></ReactBox>
          </div>
          <div className="col-6 col-md-2 ">
            <ReactNativeBox></ReactNativeBox>
          </div>
          <div className="col-6 col-md-2 ">
            <NodeJsBox></NodeJsBox>
          </div>
          <div className="col-6 col-md-2 ">
            <MongoDbBox></MongoDbBox>
          </div>
          <div className="col-6 col-md-2 ">
            <MySqlBox></MySqlBox>
          </div>
          <div className="col-6 col-md-2 ">
            <ExpressBox></ExpressBox>
          </div>
          <div className="col-6 col-md-2 ">
            <GithubBox></GithubBox>
          </div>
          <div className="col-6 col-md-2 ">
            <FirebaseBox></FirebaseBox>
          </div>
        </div>
      </div>
    </MySkillStyle>
  );
}

const MySkillStyle = styled.div`
  .MySkillTitle {
    color: #c4cfde;
  }

  .MySkillTitle_div {
    animation: MySkillTitle_div_ani 2s ease 0s infinite normal forwards;
  }
  @keyframes MySkillTitle_div_ani {
    0%,
    100% {
      transform: translateX(0%);
      transform-origin: 50% 50%;
    }

    15% {
      transform: translateX(-30px) rotate(-6deg);
    }

    30% {
      transform: translateX(15px) rotate(6deg);
    }

    45% {
      transform: translateX(-15px) rotate(-3.6deg);
    }

    60% {
      transform: translateX(9px) rotate(2.4deg);
    }

    75% {
      transform: translateX(-6px) rotate(-1.2deg);
    }
  }

  .boxStyle {
    box-shadow: 0px 0px 20px 3px #3b3f45;
  }
`;
